	import IndexSwiper from '@/components/index/IndexSwiper.vue'
	import Recommend   from '@/components/index/Recommend.vue'
	import Card        from '@/components/common/Card.vue'
	import CommondityList from '@/components/common/CommondityList.vue'
	import Banner      from '@/components/index/Banner.vue'
	import Icons       from '@/components/index/Icons.vue'
	import Hot         from '@/components/index/Hot.vue'
	import Shop        from '@/components/index/Shop.vue'

	export default {
		data() {
			return {
				//选中的索引
				topBarIndex:0,
				//顶栏滑动跟随的索引
				scrollIntoIndex:"top0",
				//数据高度
				clientHight:0,
				//顶栏数据
				topBar:[
					// {name:"推荐"},
					// {name:"运动户外"},
					// {name:"服饰内衣"},
					// {name:"鞋靴箱包"},
					// {name:"美妆个护"},
					// {name:"家居数码"},
					// {name:"食品母婴"}
					
				],
				//承载数据
				newTopBar:[
					
				]
			}
		},
		components:{
			IndexSwiper,
			Recommend,
			Card,
			CommondityList,
			Banner,
			Icons,
			Hot,
			Shop,
		},
		onLoad() {
			this.getClientHeight();
			this.__init();

			

		},
		onReady() {
// 			let view=uni.createSelectorQuery().select('.home-data');
// 			view.boundingClientRect(data => {
// 				this.clientHight=data.height;
// // console.log(data.height);
// 			}).exec();
            uni.getSystemInfo({
				success: (res) => {
				//	console.log(res);
					this.clientHight=res.windowHeight-uni.upx2px(80)-this.getClientHeight();
				}
			});
		},
		methods: {
			//getdata
			__init(){
				uni.request({
					url:"http://192.168.0.3:3000/api/index_list/data",
					success: (res) => {
						//console.log(res.data.data);
						let data=res.data.data;
						this.topBar=data.topBar;
						this.newTopBar=this.initData(data);
						
					}})
			},
			//add data
			initData(res){
				let arr=[];
				//console.log(this.topBar.length);
				for(let i=0;i<this.topBar.length;i++){
					let obj={data:[]};
					//获取首次数据
					if(i==0){
						obj.data=res.data;
					}
					arr.push(obj);
				}

				
				return arr;
			},
			//switch click topBar
			changeTab(index){
				if(this.topBarIndex===index){
					return;
				}
				this.topBarIndex=index;
				this.scrollIntoIndex='top'+index;
			},
			//finger move
			onChangeTab(e){
				this.changeTab(e.detail.current);
			},
			//获取可视区高度
			getClientHeight(){
				const res=uni.getSystemInfoSync();
				console.log(res.platform,res.statusBarHeight);
				const system=res.platform;
				if(system==='android'){
					return 48+res.statusBarHeight;
				}else if(system==='ios'){
					return 44+res.statusBarHeight;
				}else{
					return 0;
				}
			}
            
		}
	}
